/** Define consistent border, margin, and padding. */
fieldset { border: 1px solid #c0c0c0; margin: 0 2px; padding: 0.35em 0.625em 0.75em; }

/** 1. Correct `color` not being inherited in IE 8/9/10/11. 2. Remove padding so people aren't caught out if they zero out fieldsets. */
legend { border: 0; /* 1 */ padding: 0; /* 2 */ }

/** Remove default vertical scrollbar in IE 8/9/10/11. */
textarea { overflow: auto; }

/** Don't inherit the `font-weight` (applied by a rule above). NOTE: the default cannot safely be changed in Chrome and Safari on OS X. */
optgroup { font-weight: bold; }

/* Tables ========================================================================== */
/** Remove most spacing between table cells. */
table { border-collapse: collapse; border-spacing: 0; }

td, th { padding: 0; }

/** Resets all margin and padding to 0 */
body, h1, h2, h3, h4, h5, h6, p, blockquote, pre, dl, dd, ol, ul, form, fieldset, legend, figure, table, th, td, caption, hr { margin: 0; padding: 0; }

a { text-decoration: none; }

a:hover { text-decoration: underline; }

.wrapper { max-width: 63.75em; margin: 0 auto; padding: 0 0.625rem; }

@media screen and (min-width: 28.75em) { .wrapper { padding: 0 1.25rem; } }

.grid { margin-left: -0.625rem; }

.grid:after { content: ""; display: table; clear: both; }

.grid__row { margin-bottom: 20px; }

.grid__row:after { content: ""; display: table; clear: both; }

.col { box-sizing: border-box; float: left; min-height: 0.0625rem; padding-left: 0.625rem; width: 100%; margin-bottom: 10px; }

@media screen and (min-width: 42.5em) { .col { margin-bottom: 0; } }

.col--1\/3--promo-grid, .col--2\/3--promo-grid { width: 50%; }

@media screen and (min-width: 42.5em) { .col--1\/12 { width: 8.33333%; }
  .col--1\/6 { width: 16.66667%; }
  .col--1\/4 { width: 25%; }
  .col--1\/3 { width: 33.33333%; }
  .col--5\/12 { width: 41.66667%; }
  .col--1\/2 { width: 50%; }
  .col--7\/12 { width: 58.33333%; }
  .col--2\/3 { width: 66.66667%; }
  .col--3\/4 { width: 75%; }
  .col--5\/6 { width: 83.33333%; }
  .col--11\/12 { width: 91.66667%; } }

.styleguide__grid-item { height: 3.125rem; background: #000; width: 100%; vertical-align: middle; text-align: center; color: #fff; }

.styleguide__section { margin-bottom: 2.5rem; }

.styleguide__section-title { margin: 1.875rem 0; padding-bottom: 0.625rem; border-bottom: 1px solid #000; }

.styleguide__btn-section a { margin: 0.625rem; }

.color-list { background: #e6e6e6; padding-left: 1%; }

.color-item { display: inline-block; padding: 0.625rem; width: 32%; }

@media screen and (min-width: 42.5em) { .color-item { width: 24%; } }

@media screen and (min-width: 60em) { .color-item { width: 12%; } }

.color-item__color { border-radius: 0.1875rem 0.1875rem 0 0; display: block; height: 3.75rem; }

.color-item__title { background: #fff; border-radius: 0 0 0.1875rem 0.1875rem; display: block; padding: 0.625rem 0.3125rem; width: 100%; }
